
import React from 'react';
import { Plus, Eye, Trash2, Globe, Edit3, Lock } from 'lucide-react';
import { HostedSite } from '../types';
import { Button } from '../components/Button';

interface DashboardProps {
  sites: HostedSite[];
  onCreateNew: () => void;
  onViewSite: (site: HostedSite) => void;
  onEditSite: (site: HostedSite) => void;
  onDeleteSite: (id: string) => void;
}

export const Dashboard: React.FC<DashboardProps> = ({ sites, onCreateNew, onViewSite, onEditSite, onDeleteSite }) => {
  const formatDate = (timestamp: number | undefined) => {
    if (!timestamp) return 'N/A';
    try {
      return new Date(timestamp).toLocaleDateString();
    } catch {
      return 'Invalid Date';
    }
  };

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="flex justify-between items-end mb-12 border-b border-charcoal/10 pb-6">
        <div>
          <h1 className="text-5xl font-serif text-charcoal mb-2">Dashboard</h1>
          <p className="text-charcoal-light font-sans tracking-wide">Manage your digital creations.</p>
        </div>
        <Button variant="primary" size="lg" onClick={onCreateNew} className="rounded-full bg-charcoal text-cream-100 hover:bg-charcoal-light">
          <Plus className="w-5 h-5 mr-2" />
          Create New
        </Button>
      </div>

      {sites.length === 0 ? (
        <div className="bg-cream-100 rounded-3xl p-20 text-center border-2 border-dashed border-charcoal/10">
          <div className="mx-auto w-24 h-24 bg-white rounded-full flex items-center justify-center mb-8 shadow-sm">
            <Globe className="w-10 h-10 text-charcoal" />
          </div>
          <h3 className="text-3xl font-serif text-charcoal mb-4">No sites yet</h3>
          <p className="text-charcoal-light max-w-md mx-auto mb-10 font-sans">
            Start your journey by uploading HTML or letting our AI architect your first page.
          </p>
          <Button variant="primary" onClick={onCreateNew} className="rounded-full px-8 py-3 bg-accent-green text-charcoal font-bold hover:bg-accent-green/90 border-none">
            Create First Site
          </Button>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {sites.map((site) => (
            <div key={site.id} className="group bg-cream-100 rounded-2xl overflow-hidden transition-all hover:shadow-xl hover:-translate-y-1 flex flex-col">
              {/* Preview Thumbnail */}
              <div
                className="h-56 bg-white relative overflow-hidden cursor-pointer m-2 rounded-xl border border-charcoal/5"
                onClick={() => onViewSite(site)}
              >
                {site.htmlContent ? (
                  <iframe
                    srcDoc={site.htmlContent}
                    className="w-[200%] h-[200%] transform scale-50 origin-top-left pointer-events-none select-none bg-white"
                    title={site.title}
                    tabIndex={-1}
                    loading="lazy"
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center text-slate-300">
                    <Globe className="w-16 h-16 opacity-20" />
                  </div>
                )}
                {/* Hover Overlay */}
                <div className="absolute inset-0 bg-charcoal/0 group-hover:bg-charcoal/5 transition-opacity flex items-center justify-center">
                  <div className="opacity-0 group-hover:opacity-100 transition-all transform translate-y-2 group-hover:translate-y-0 bg-white px-5 py-2 rounded-full shadow-md font-bold text-charcoal text-sm">
                    Preview
                  </div>
                </div>
              </div>

              <div className="p-6 flex-1 flex flex-col">
                <div className="flex justify-between items-start mb-4">
                  <h3 className="text-2xl font-serif text-charcoal line-clamp-1" title={site.title}>
                    {site.title}
                  </h3>
                  <span className={`px-2 py-1 rounded text-[10px] font-bold uppercase tracking-wider ${site.published ? 'bg-green-100 text-green-800' : 'bg-yellow-100 text-yellow-800'}`}>
                    {site.published ? 'Live' : 'Draft'}
                  </span>
                </div>

                <div className="mt-auto pt-4 border-t border-charcoal/5 flex items-center justify-between">
                  <div className="text-xs font-mono text-charcoal/50">
                    {formatDate(site.updatedAt || site.createdAt)}
                  </div>
                  <div className="flex space-x-1">
                    <Button variant="ghost" size="sm" onClick={() => onEditSite(site)} className="hover:bg-white rounded-full w-8 h-8 p-0 flex items-center justify-center text-charcoal">
                      <Edit3 className="w-4 h-4" />
                    </Button>
                    <Button variant="ghost" size="sm" onClick={() => onViewSite(site)} className="hover:bg-white rounded-full w-8 h-8 p-0 flex items-center justify-center text-charcoal">
                      <Eye className="w-4 h-4" />
                    </Button>
                    <Button variant="ghost" size="sm" className="hover:bg-red-50 text-red-400 hover:text-red-600 rounded-full w-8 h-8 p-0 flex items-center justify-center" onClick={() => onDeleteSite(site.id)}>
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
